import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ApiService } from '../../COMMON/api.service'
@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.css']
})
export class SigninComponent implements OnInit {
  adminSignIn: FormGroup;
  submitted: boolean = false
  constructor(private fb: FormBuilder, private apiSer: ApiService) { }

  ngOnInit(): void {
    this.adminSignIn = this.fb.group({
      loginId: ['', Validators.required],
      password: ['', Validators.required]
    })
    this.getUsersList()
  }

  get signInControls(){
    return this.adminSignIn.controls
  }

  onSubmit(){
    this.submitted = true
    console.log(this.adminSignIn.status)
    if(this.adminSignIn.status == 'VALID'){
      console.log(this.adminSignIn.value)
      var input = {
        Email: this.adminSignIn.value.loginId,
        Password: this.adminSignIn.value.password,
        ConfirmPassword: '@aA123456',
        User_Name: 'Siva Ravula'
      }
      this.apiSer.userLogin(input).subscribe((data: any) => {
        console.log(data)
        this.getUsersList()
      })
    }
  }

  getUsersList(){
    this.apiSer.getRegistredUsers().subscribe((data: any) => {
      console.log(data)
    })
  }

}
